import React, { useState, useEffect } from 'react';
import { Typography, Box, Tab } from '@ya.praktikum/react-developer-burger-ui-components';
import burgerIngredientsStyles from './burgerIngredients.module.css';
import IngredientsGroup from '../IngredientsGroup/IngredientsGroup.jsx';
import { useSelector, useDispatch } from 'react-redux';
import { getIngredients } from '../../services/actions/ingredients.js';
import { useInView } from 'react-hook-inview';



const BurgerIngredients = ({ onIngredientClick }) => {
    
    const dispatch = useDispatch();
    const {ingredients, ingredientsRequest} = useSelector(state => state.ingredients);

    //  useInView      
    const [bunsRef, inViewBuns] = useInView({ threshold: 0.35 });
    const [saucesRef, inViewSauces] = useInView({ threshold: 0.4 });
    const [mainRef, inViewMain] = useInView({ threshold: 0.2 });

    useEffect(() => {
        if (inViewBuns) {
            setCurrentTab("");
        } else if (inViewSauces) {
            setCurrentTab("");
        } else if (inViewMain) {
            setCurrentTab("");
        }
    }, [inViewBuns, inViewMain, inViewSauces]);


    // 
    useEffect(
        () => {
            dispatch(getIngredients());
        }, 
        []);   
    

    //   
    const onTabClick = (tab) => {
        setCurrentTab(tab);
        const element = document.getElementById(tab);
        if (element) element.scrollIntoView({ behavior: "smooth" });
    };

    
    const [currentTab, setCurrentTab] = useState('');
    //      
    const filterByType = (ingredientsArray, typeName) => {
        return ingredientsArray.filter((ingredient) => ingredient.type === typeName);
    }
    
        return(
            <section className={`mr-10 ${burgerIngredientsStyles.section}`}>
                <h1 className="mb-5 text text_type_main-large"> </h1>
                <div style={{ display: 'flex' }}>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                    <Tab value="" active={currentTab === ''} onClick={onTabClick}>
                        
                    </Tab>
                </div>
                {ingredientsRequest &&
                    <p className={`text text_type_main-large ${burgerIngredientsStyles.loader}`}>...</p>
                }
                {!ingredientsRequest &&
                <ul className={burgerIngredientsStyles.list}>
                    <IngredientsGroup scrollRef={bunsRef} titleId="" ingredients={filterByType(ingredients, "bun")} onIngredientClick={onIngredientClick}></IngredientsGroup>
                    <IngredientsGroup scrollRef={saucesRef} titleId="" ingredients={filterByType(ingredients, "sauce")} onIngredientClick={onIngredientClick}></IngredientsGroup>
                    <IngredientsGroup scrollRef={mainRef} titleId="" ingredients={filterByType(ingredients, "main")} onIngredientClick={onIngredientClick}></IngredientsGroup>
                </ul>    
                }         
            </section>
        )
}






export default BurgerIngredients;